.header {
  background-color: var(--clr-accent);
  height: 10vh;
  min-height: 50px;
  color: var(--clr-light);
  position: fixed;
  width: 100vw;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 99;
  padding: 10px;
  box-sizing: border-box;
}
